<template>
    <div>
        <h1>登录成功</h1>

        <h3>欢迎你{{msg}}</h3>
        <h4></h4>
    </div>
</template>
<script>
export default {
    name:'success-vue',
    data(){
        return{
            msg:''
        }
    },
    created(){
        let user = localStorage.getItem('user');
        user = JSON.parse(user);
        this.msg = user.employee.userName;
    }
}
</script>



