</template>
<script>

export default {
    name:'cssAnimista',
    data(){
        return{

        }
    }
}
</script>
<style scoped lang="scss">
a{
    color: blue;
}
.box{
    width: 100px;
    height: 100px;
    background: rgb(107, 240, 240);
    margin: 20px auto;
    // position: relative;
    // left:20px;
    // top: 200px;
    // animation-name: example;
    // animation-duration: 4s;
}
.slide-fwd-center {
	-webkit-animation: slide-fwd-center 0.45s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: slide-fwd-center 0.45s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
// .slide-fwd-center{animation:slide-center .45s cubic-bezier(.25,.46,.45,.94) both}
