<template>
	<div class="content">
		<h2 class="title">Toast提示</h2>
		<div class="toast">
			<el-button type="primary" @click="handleInfo('success')">成功</el-button>
			<el-button type="danger" @click="handleInfo('error')">失败</el-button>
			<el-button type="warning" @click="handleInfo('warning')">警告</el-button>
			<el-button type="default" @click="handleInfo('info')">提示</el-button>
			<el-button type="default" @click="handleInfo('success2')">默认提示</el-button>
			<el-button type="default" @click="handleInfo('back')">返回</el-button>
		</div>
	</div>
</template>
<script>
	export default {
		name: "toast",
		methods: {
			handleInfo(type) {
				switch (type) {
					case "success":
						this.$toast({ message: "成功提示", type: "success" });
						break;
					case "error":
						this.$toast({ message: "失败", type: "error" });
						break;
					case "warning":
						this.$toast({ message: "警告", type: "warning" });
						break;
					case "info":
						this.$toast({ message: "提示", type: "info" });
						break;
					case "success2":
						this.$toast.success("恭喜恭喜");
						break;
					default:
						this.$router.back();
						break;
				}
			}
		}
	};
</script>
<style scoped lang="scss">
	.content {
		margin: 30px;
		display: flex;
		flex-direction: column;
	}
	.toast {
		width: 200px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		button {
			width: 120px;
			margin: 20px 0;
		}
	}
</style>



