<template>
	<el-dialog
		title="图片裁剪"
		:visible.sync="dialogVisible"
		:id="cropperIdName+`_cropper`"
		width="800px"
		:close-on-click-modal="false"
		:before-close="handleClose"
	>
		<div style="width:100%">
			<div class="left" :style="`width:calc(100% - (${styleImg.width} + 20px))`">
				<div>
					<el-upload
						class="upload-demo"
						ref="upload"
						:multiple="multiple"
						:accept="accept"
						:id="cropperIdName+`_upload`"
						:action="actionUrl"
						:headers="headersUpload"
						:on-change="handleChange"
						:file-list="fileList"
						:show-file-list="showFileList"
						:auto-upload="autoUpload"
					>
						<el-button slot="trigger" size="small" type="primary">请选择图片</el-button>
						<el-button
							style="margin-left: 10px;"
							size="small"
							v-if="!autoUpload && isShowUploadBtn"
							type="success"
							@click="submitUpload"
						>上传</el-button>
					</el-upload>
					<el-button
						v-show="isButton"
						:id="cropperIdName+`_button`"
						style="margin-left: 10px;"
						@click="crop"
					>裁剪</el-button>
					<el-button v-show="isButton" :id="cropperIdName+`_cancelButton`" @click="cancel">取消</el-button>
				</div>
				<div class="bigImg" :style="`width:calc(100% - 10px)`">
					<img v-show="panel" :id="cropperIdName+`_cropper_img`" :src="url" alt="Picture" />
				</div>
			</div>
			<div class="right">
				<div class="cropper-right-title">裁剪预览图：</div>
				<div class="smallImg" :style="styleImg" :id="cropperIdName+`_cropper_smallImg`">
					<img
						class="picture"
						v-show="isPreview"
						:id="cropperIdName+`_cropper_picture`"
						:src="headerImage"
					/>
				</div>
			</div>
		</div>
		<span slot="footer">
			<el-button type="default" @click="handleClose">取消</el-button>
			<el-button type="success" @click="submitUpload">上传</el-button>
		</span>
	</el-dialog>
</template>
<script>
	import Cropper from "cropperjs";
	import "cropperjs/dist/cropper.min.css";
	// import serverPath from "@/xlib/niceloo/apiserver/serverPath.js";
	// import io from '@/xlib/niceloo/apiserver/io.js';
	// import "@/xlib/niceloo/api/api-kaowubaoming/api..upload-file.js";
	// var niceloo = (new Function("return this"))().niceloo;

	export default {
		name: "image-cropper",
		props: {
			cropperIdName: {
				type: String,
				default: "demo"
			},
			fileType: {
				type: Number
			},
			actionUrl: {
				// 上传地址
				type: String,
				default() {
					return "upload/files";
				}
			},
			headersUpload: {
				// header
				type: Object,
				default() {
					return {
						Authorization: ""
					};
				}
			},
			autoUpload: {
				// 是否自动上传
				type: Boolean,
				default: false
			},
			aspectRatioWidth: {
				// 证件照：5/7，身份证件照：16/5，证书比例：10/7
				type: Number,
				default: 5
			},
			aspectRatioHeight: {
				// 证件照：5/7，身份证件照：16/5，证书比例：10/7
				type: Number,
				default: 7
			},
			zoomable: {
				// 缩放
				type: Boolean,
				default: true
			},
			cropperUrl: {
				// 裁剪之后的url
				type: String,
				default: ""
			},
			showFileList: {
				// 是否展示上传的列表：默认不展示
				type: Boolean,
				default: false
			},
			isShowUploadBtn: {
				type: Boolean,
				default: false
			},
			styleImg: {
				type: Object,
				default() {
					return {
						width: "120px",
						height: "168px"
					};
				}
			},
			multiple: {
				// 是否多选
				type: Boolean,
				default: false
			},
			accept: {
				// 文件类型
				type: String,
				default: ".jpeg,.jpg,.png"
			}
		},
		data() {
			return {
				dialogVisible: false,
				headerImage: this.cropperUrl, // 裁剪之后的图片
				cropper: "",
				url: "", // 上传的原图
				fileList: [],
				imgType: "", // 图片类型
				imgName: "", // 图片名称
				fileCropperBlob: null,
				fileBlob: null, // 文件流blob
				isPreview: false, // 是否预览
				isButton: false, // 裁剪按钮
				croppable: false,
				isCropper: false, // 是否点击了裁剪事件
				panel: false // 是否展示原图
			};
		},
		watch: {
			cropperUrl(newV) {
				this.headerImage = newV;
			}
		},
		methods: {
			show() {
				this.dialogVisible = true;
			},

			// 点击确定按钮，获取裁剪/原图的url
			submit() {
				if (!this.isCropper) {
					var reader = new FileReader();
					reader.readAsDataURL(this.fileBlob);
					reader.onload = () => {
						this.$emit("submit", reader.result, this.fileBlob);
					};
				} else {
					this.cropImg();
					this.$emit("submit", this.headerImage, this.fileCropperBlob);
				}
				if (this.isCropper) {
					this.cancel();
				}

				this.clear();
			},
			// 关闭弹框
			handleClose() {
                console.log('cloase')
				this.dialogVisible = false;
				this.clear();
				if (this.isCropper) {
					this.cancel();
				}
				this.$emit("close");
			},
			// 上传文件时的change
			handleChange(file, files) {
				this.panel = true;
				this.isButton = true;
				this.url = this.getObjectURL(file.raw);
				this.imgType = file.raw.type;
				this.imgName = file.raw.name;
                this.cropper = false;
				this.fileBlob = file.raw;
				// // 每次替换图片要重新得到新的url
				if (this.cropper) {
					this.cropper.replace(this.url);
				}
				this.panel = true;
				this.fileList = files;
			},
			// 获取url
			getObjectURL(file) {
				var url = null;
				if (window.createObjectURL !== undefined) {
					// basic
					url = window.createObjectURL(file);
				} else if (window.URL !== undefined) {
					// mozilla(firefox)
					url = window.URL.createObjectURL(file);
				} else if (window.webkitURL !== undefined) {
					// webkit or chrome
					url = window.webkitURL.createObjectURL(file);
				}
				return url;
			},
			// 清楚事件
			clear() {
				this.headerImage = ""; // 预览值即裁剪之后的图片地址
				this.url = ""; // 上传的原图
				this.isPreview = false;
				this.panel = false;
				this.isButton = false;
			},

			// 裁剪的取消事件
			cancel() {
                this.isCropper = false;
				this.cropper.clear();
				this.cropper.reset();
				this.cropper.destroy(); // 销毁
			},
			// 裁剪--获取裁剪框
			crop() {
				this.init();
				this.isCropper = true;
			},
			// 初始化这个裁剪框
			init() {
				let self = this;
				let image = document.getElementById(
					`${this.cropperIdName + "_cropper_img"}`
				);
				this.cropper = new Cropper(image, {
					aspectRatio: this.aspectRatioWidth / this.aspectRatioHeight,
					viewMode: 0,
					background: false,
					autoCropArea: 0.4,
					guides: false, // 是否展示虚线
					preview: `#${this.cropperIdName}_cropper_smallImg`,
					zoomable: this.zoomable,
					ready: function() {
						self.croppable = true;
					}
				});
			},
			// 获取裁剪之后的图片
			cropImg() {
				var croppedCanvas;
				var roundedCanvas;
				if (!this.croppable) {
					return;
				}
				// Crop
				croppedCanvas = this.cropper.getCroppedCanvas();
				// Round
				roundedCanvas = this.getRoundedCanvas(croppedCanvas);
				this.headerImage = roundedCanvas.toDataURL(this.imgType);

				this.isPreview = true;
				this.fileCropperBlob = this.dataURLtoFile(
					this.headerImage,
					this.imgName
				);
			},
			// base64转file文件
			dataURLtoFile(dataurl, filename) {
				// 将base64转换为文件
				var arr = dataurl.split(","),
					mime = arr[0].match(/:(.*?);/)[1],
					bstr = atob(arr[1]),
					n = bstr.length,
					u8arr = new Uint8Array(n);
				while (n--) {
					u8arr[n] = bstr.charCodeAt(n);
				}
				return new File([u8arr], filename, { type: mime });
			},
			// 画图
			getRoundedCanvas(sourceCanvas) {
				var canvas = document.createElement("canvas");
				var context = canvas.getContext("2d");
				var width = sourceCanvas.width;
				var height = sourceCanvas.height;
				canvas.width = width;
				canvas.height = height;
				context.imageSmoothingEnabled = true;
				context.drawImage(sourceCanvas, 0, 0, width, height);
				context.globalCompositeOperation = "destination-in";
				context.beginPath();
				context.fill();
				return canvas;
			},
			// 手动上传
			submitUpload() {
				if (!this.isCropper) {
					var reader = new FileReader();
					reader.readAsDataURL(this.fileBlob);
					reader.onload = () => {
						this.headerImage = reader.result;
						this.$emit("submit", this.headerImage);
						// this.uploadFileRaw(this.headerImage, this.fileBlob);
					};
				} else {
					this.cropImg();
					this.$emit("submit", this.headerImage);
					// this.uploadFileRaw(this.headerImage, this.fileCropperBlob);
				}
                this.handleClose();
			},
			uploadFileRaw(file) {
				let xhr, formData;
				// let Authorization = "Bearer" + " " + niceloo.app.visitor.getToken();

				// xhr = io.listenXMLHttpRequestStateChange({
				// 	onsuccess: res => {
				// 		this.headerImage = res.url;
				// 		this.$message.success("上传成功");

				// 		this.$emit("upload-file-raw", res.url, this.cropperIdName);
				// 	},
				// 	onerror: res => {
				// 		this.$alert(`${res.getDescription()}`, {
				// 			confirmButtonText: "确定"
				// 		});
				// 	},
				// 	oncomplete: function() {}
				// });

				// xhr.open("POST", this.actionUrl);
				// // /**请求头部信息设置*/
				// xhr.setRequestHeader("Authorization", Authorization);
				// xhr.setRequestHeader(
				// 	"systemCode",
				// 	niceloo.app.$setting.getTitleCategoryId()
				// );

				formData = new FormData();
				formData.append("file", file); // 此处与源文档不一样
				formData.append("fileTypeId", this.fileType); // 此处与源文档不一样
				xhr.send(formData);
			}
		}
	};
</script>
<style scoped src="./index1.scss" lang="scss"/>



