<template>
	<transition
		name="toast-fade"
		@after-leave="handleAfterLeave"
		@mouseenter="clearTimer"
		@mouseleave="startTimer"
	>
		<div
			:id="id"
			:class="[
				'el-toast_wrapper',
				customClass,
				type && !iconClass ? `toast-${ type }` : '',
				{center: center}
			]"
			v-show="visible"
			:style="{ top: verticalOffset + 'px','z-index':zIndex }"
			role="alert"
		>
			<i :class="[icon,'el-icon']" />
			<slot>
				<p v-if="dangerouslyUseHTMLString" v-html="message" />
				<p v-else>{{ message }}</p>
			</slot>
			<i class="el-close gezi icon-guanbi" v-if="showClose" @click="close" />
		</div>
	</transition>
</template>

<script>
	export default {
		data() {
			return {
				id: "", // 唯一文本id
				// 提示消息文本
				message: "",
				duration: 3000,
				// 类型
				type: "info", // 状态类型 info:默认提示，success:成功，error：失败，warning:警告
				// 显示/隐藏
				visible: false,
				// 定位高度
				center: false, // 文字居中
				showClose: false, // 关闭按钮
				dangerouslyUseHTMLString: false, // 使用HTML片段
				iconClass: "", // 自定义图标的类名，会覆盖 type
				customClass: "", // 自定义类名
				zIndex: 2000,
				timer: null,
				onClose: null, // 关闭事件 function
				closed: false, // 是否操作关闭按钮
				verticalOffset: 20 // 偏移
			};
		},
		computed: {
			// 图标
			icon() {
				let icon = "";
				switch (this.type) {
					case "success":
						icon = "gezi icon-a-changyong_chenggongfuben";
						break;
					case "info":
						icon = "gezi icon-tishi";
						break;
					case "warning":
						icon = "gezi icon-a-changyong_tishifuben";
						break;
					case "error":
						icon = "gezi icon-a-changyong_cuowufuben";
						break;
					case "":
						icon = "";
						break;
				}
				icon = this.iconClass ? this.iconClass : icon;
				return icon;
			},
		},
		watch: {
			closed(newVal) {
				if (newVal) {
					this.visible = false;
				}
			},
		},
		methods: {
			/**
			 * @description: message显示
			 */
			messageShow() {
				this.visible = true;
			},

			/**
			 * @description: message隐藏
			 */
			messageHide() {
				this.visible = false;
			},

			/**
			 * @description: 销毁组件
			 */
			handleAfterLeave() {
				this.$destroy(true);
				this.$el.parentNode.removeChild(this.$el);
			},

			// 清除计时
			clearTimer() {
				clearTimeout(this.timer);
			},

			// 开始计时
			startTimer() {
				if (this.duration > 0) {
					this.timer = setTimeout(() => {
						if (!this.closed) {
							this.close();
						}
					}, this.duration);
				}
			},
			// 关闭事件
			close() {
				this.closed = true;
				if (typeof this.onClose === "function") {
					this.onClose(this);
				}
			},
			// 键盘事件
			keydown(e) {
				if (e.keyCode === 27) {
					// esc关闭消息
					if (!this.closed) {
						this.close();
					}
				}
			}
		},
		// 初始化
		mounted() {
			this.startTimer();
			document.addEventListener("keydown", this.keydown);
		},
		// 销毁
		beforeDestroy() {
			document.removeEventListener("keydown", this.keydown);
		}
	};
</script>

<style scoped lang="scss">
	.el-toast_wrapper {
		min-width: 380px;
		box-sizing: border-box;
		border-radius: 4px;
		border-width: 1px;
		border-style: solid;
		border-color: #ebeef5;
		position: fixed;
		left: 50%;
		top: 20px;
		transform: translateX(-50%);
		background-color: #edf2fc;
		transition: opacity 0.3s, transform 0.4s, top 0.4s;
		overflow: hidden;
		padding: 15px 15px 15px 20px;
		display: flex;
		align-items: center;
		z-index: 2000;
		font-size: 14px;
		color: #909399;
		&.toast-success {
			background: #f0f9eb;
			border-color: #e1f3d8;
			color: #67c23a;
		}
		&.toast-warning {
			background: #fdf6ec;
			border-color: #faecd8;
			color: #e6a23c;
		}
		&.toast-error {
			background: #fef0f0;
			border-color: #fde2e2;
			color: #f56c6c;
		}
		&.toast-info {
			background: #edf2fc;
			color: #909399;
		}
		.el-icon {
			margin-right: 10px;
			width: 14px;
			height: 14px;
		}
		&.center {
			text-align: center;
		}
		p {
			padding-right: 16px;
			margin: 0;
		}
		.el-close {
			position: absolute;
			top: 50%;
			right: 15px;
			transform: translateY(-50%);
			cursor: pointer;
			color: #c0c4cc;
			font-size: 16px;
			font-style: normal;
			&:hover{
				color: #333;
			}
		}
	}
	.toast-fade-enter-active {
		-webkit-animation: toast-fade-in 0.3s;
		animation: toast-fade-in 0.3s;
	}
	.toast-fade-leave-active {
		-webkit-animation: toast-fade-out 0.3s;
		animation: toast-fade-out 0.3s;
	}
	.toast-fade-enter, .toast-fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
		opacity: 0;
	}

	@keyframes toast-fade-in {
		0% {
			transform: translate3d(-50%, -20px, 0);
			opacity: 0;
		}
		100% {
			transform: translate3d(-50%, 0, 0);
			opacity: 1;
		}
	}
	@keyframes toast-fade-out {
		0% {
			transform: translate3d(-50%, 0, 0);
			opacity: 1;
		}
		100% {
			transform: translate3d(-50%, -20px, 0);
			opacity: 0;
		}
	}
</style>



