<template>
	<div class="content">
        <div>
			<el-button class="go-back" @click="goBack">返回</el-button>
		</div>
        <tinymce :height="500" />
    </div>
</template>
<script>
import tinymce from "../../comps-t/tinymce-base/index.vue"
	export default {
		name: "toast",
        components:{
            tinymce
        },
		methods: {
            goBack() {
				history.back();
			},
		}
	};
</script>
<style scoped lang="scss">
	.content {
		margin: 30px;
		display: flex;
		flex-direction: column;
	}
	.toast {
		width: 200px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		button {
			width: 120px;
			margin: 20px 0;
		}
	}
</style>



