<template>
    <div>
        <h1>人脸识别</h1>
        <el-button type="primary" @click="handleFace">人脸识别</el-button>
        <face-component :isOpen="isOpen" />
    </div>
</template>
<script>
import FaceComponent from "./comp/face.vue"
export default {
    name:'face-index',
    components:{
        FaceComponent
    },
    data(){
        return{
            isOpen:false,
        }
    },
    methods:{
        handleFace(){
            this.isOpen = true;
        }
    }
}
</script>



