<template>
	<div class="rich-text-editor">
		<editor
			v-model="html"
			id="editor"
			:disabled="disabled"
			:placeholder="placeholder"
			:init="setting"
			@input="getInput"
		/>
	</div>
</template>

<script>
	import Editor from "@tinymce/tinymce-vue";
	// import Lang from "../../plugins/tinymce-5.4.2-90/langs/zh_CN.js";
	import Lang from "../../../static/tinymce/lang/zh_CN";

	// import '@/xlib.01/api/api-kaowuguanli/api..upload-files.js'

	// import serverPath from '@youlu/niceloo/libs/@xlib.01/niceloo/apiserver/serverPath.js'
	// import io from '@youlu/niceloo/libs/@xlib.01/niceloo/apiserver/io.js'

	// var niceloo = new Function('return this')().niceloo

	/**
	 * @property {String} value 富文本输入值
	 * @property {String} placeholder 提示
	 * @property {Number} height 富文本高度
	 * @property {Number} width 富文本宽度
	 * @property {Boolean} disabled 禁用富文本
	 * @property {String | Array} plugins 插件
	 * @property {String | Array} toolbar 工具栏按钮
	 * @method getInput editor传递富文本的值 父组件可获取富文本值
	 * @method uploadImg 上传图片
	 * <tinymce v-model="form.info" :height="500" :width="" :plugins="" :toolbar="" @input=""/>
	 */
	export default {
		name: "tinymce",
		components: {
			Editor
		},
		props: {
			// 输入值
			value: {
				type: String,
				default: ""
			},
			placeholder: {
				type: String,
				default() {
					return "请输入...";
				}
			},
			// 高
			height: {
				type: Number,
				default() {
					return 200;
				}
			},
			// 宽
			width: {
				type: [String, Number],
				default() {
					return "100%";
				}
			},
			// 禁用
			disabled: {
				type: Boolean,
				default: false
			},
			// 插件
			plugins: {
				type: [String, Array],
				default:
					"code link lists imageupload media preview table fullscreen paste lineheight"
			},
			// 工具栏按钮
			toolbar1: {
				type: [String, Array],
				default:
					" undo redo| fontselect |formatselect | fontsizeselect | forecolor backcolor | bold italic underline strikethrough |imageupload |table"
			},
			toolbar2: {
				type: [String, Array],
				default:
					"alignleft aligncenter alignright alignjustify | bullist numlist | outdent indent | lineheight |subscript superscript  |cut copy paste|  link unlink | media |code preview removeformat|fullscreen  "
			}
		},
		watch: {
			// 监听富文本输入的值
			value: {
				deep: true,
				handler(newVal) {
					this.html = newVal;
				}
			},
			html(val) {
				this.$emit("update:value", val);
			}
		},
		data() {
			return {
				setting: {
					selector: "editor", // 富文本id
					language: "zh_CN", // 语言
					language_url: Lang,
					// 插件
					plugins: this.plugins,
					// 工具栏
					toolbar1: this.toolbar1,
					toolbar2: this.toolbar2,
					height: this.height,
					width: this.width,
					menubar: true, // 隐藏菜单栏
					branding: false, // 隐藏右下角技术支持
					toolbar_mode: "wrap", // 工具栏换行
					resize: true, // 调整编辑器大小工具 true（仅允许改变高度）, false（完全不让你动）, 'both'（宽高都能改变，注意引号）
					paste_remove_styles_if_webkit: true, // 设为false可禁用TinyMCE默认针对webkit生成样式的过滤器。
					paste_data_images: true, // 粘贴的同时能把内容里的图片自动上传，非常强力的功能
					paste_convert_word_fake_lists: true, // 插入word文档需要该属性
					paste_auto_cleanup_on_paste: true,
					paste_enable_default_filters: true, // 开启默认过滤器
					paste_remove_styles: true,
					paste_strip_class_attributes: true,
					paste_as_text: false, // 粘贴为文本
					smart_paste: false, // 发现看起来像是url的文本就给它加上链接。发现看起来像是图片地址的东西，然后尝试用图片替换掉这段地址。
					headers: this.headersUpload,
                    lineheight_formats:'1 1.5 2 2.5 3 3.5 4 4.5 5', // 行高设置
                    content_style:"*{margin:0; padding:0}", // 自定义tinymce样式
					entity_encoding: "raw", // 禁止‘’转义字符
					images_upload_handler: (blobInfo, success, failure) => {
						this.uploadImg("paste", blobInfo, success, failure);
					},
					// 自定义批量上传图片插件
					click_imgs_callback: editor => {
						let success, failure;
						let input_f = document.createElement("input");
						input_f.type = "file";
						input_f.name = "file";
						input_f.accept = "image/jpg,image/jpeg,image/png";
						input_f.multiple = "multiple";
						input_f.onchange = file => {
							for (let i = 0; i < file.target.files.length; i++) {
								this.uploadImg(
									"custom",
									file.target.files[i],
									success,
									failure,
									editor
								);
							}
						};
						input_f.click();
					}
				},
				html: this.value,
				// 接口地址
				actionUrl: "upload/files/image", // 需要拼接
				headersUpload: {} // 请求头部
			};
		},
		mounted() {
			// this.headersUpload = {
			// 	Authorization: "Bearer" + " " + niceloo.app.visitor.getToken()
			// };
			// 获取请求token
			this.headersUpload = {
				// Authorization: "Bearer" + " " + niceloo.app.visitor.getToken()
			};
		},
		methods: {
			// editor传递富文本的值
			getInput() {
				this.$emit("input", this.html);
			},
			uploadImg(type, blobInfo, success, failure, editor) {
				let file = blobInfo;
				if (type === "paste") {
					file = blobInfo.blob();
				}
				let xhr, formData;
				let Authorization = "Bearer" + " " + niceloo.app.visitor.getToken();
				// 此处请根据原生ajax请求接口数据（每个接口的请求信息是不同的，可能有点问题哦~，需要根据实际接口重写ajax请求）
				xhr = new XMLHttpRequest();
				// xhr = io.listenXMLHttpRequestStateChange({
				// 	onsuccess: (data) => {
				// 		if (type === "paste") {
				// 			// 复制粘贴
				// 			success(data.url);
				// 		} else {
				// 			// 手动上传
				// 			editor.insertContent(`<img src="${data.url}"/>`);
				// 		}
				// 	},
				// 	onerror: (failures) => {
				// 		if (type === "paste") {
				// 			failure(failures.getDescription());
				// 		} else {
				// 			this.$alert(`${failures.getDescription()}`, {
				// 				confirmButtonText: "确定",
				// 			});
				// 		}
				// 	},
				// 	oncomplete: function () {},
				// });
				xhr.open("POST", this.actionUrl);
				xhr.onreadystatechange = function() {
					// readyState == 4说明请求已完成
					if (xhr.readyState == 4) {
						if (xhr.status == 200 || xhr.status == 304) {
							if (type === "paste") {
								// 复制粘贴
								success(xhr.responseText.url);
							} else {
								// 手动上传
								editor.insertContent(
									`<img src="${xhr.responseText.url}"/>`
								);
							}
							console.log(xhr.responseText);
							callback(xhr.responseText);
						} else {
						}
						if (type === "paste") {
							failure(failures.getDescription());
						} else {
							this.$alert(`${failures.getDescription()}`, {
								confirmButtonText: "确定"
							});
						}
					}
				};
				/** 请求头部信息设置 */
				xhr.setRequestHeader("Authorization", Authorization);
				xhr.setRequestHeader("Accept", "application/json");
				formData = new FormData();
				formData.append("file", file); // 此处与源文档不一样
				xhr.send(formData);
			}
		}
	};
</script>
<style scoped lang="scss">
	.rich-text-editor .tox-tinymce .tox-toolbar .tox .tox-tbtn {
		width: 32px;
	}
</style>



