<template>
	<div class="box">
		<el-form class="form" :inline="true" label-width="90">
			<el-form-item label="name">
				<el-input type="text" v-model="form.name" placeholder="请输入名称"></el-input>
			</el-form-item>
			<el-form-item label="password">
				<el-input type="text" v-model="form.password" placeholder="请输入描述"></el-input>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="submit">新增</el-button>
			</el-form-item>
		</el-form>
		<div class="info">
			<p v-for="(item,index) in list" :key="index">
				<el-input v-model="item.name"></el-input>
				<el-input v-model="item.password"></el-input>
				<el-button type="text" @click="edit(item)">编辑</el-button>
				<el-button type="text" @click="del(item._id)">删除</el-button>
			</p>
		</div>
	</div>
</template>
<script>
	import { api } from "../../api/api.js";
	// import axios from 'axios';
	export default {
		name: "mongodb-view",
		data() {
			return {
				form: {
					name: "",
					password: ""
				},
				list: []
			};
		},
		created() {
			this.query();
		},
		methods: {
			submit() {
				api.updateCustomIteration(this.form)
					.then(res => {
                        if(res){
                            this.$toast.success("更新成功");
                            this.query();
                            this.form = { name: "", password: "" };
                        }

					})
			},
            edit(item){
                api.updateCustomIteration(item)
					.then(res => {
                        if(res){
                            this.$toast.success("更新成功");
                            this.query();
                            this.form = { name: "", password: "" };
                        }
					})
            },
			del(id) {
				api.delCustomIteration({ id: id })
					.then(res => {
						console.log(res, "res-del");
						this.$toast.success("删除成功");
						this.query();
					})
			},
			query() {
				api.queryCustomIteration()
					.then(res => {
						this.list = res;
						console.log(res, "res-query");
					})
			}
		}
	};
</script>
<style scoped lang="scss">
	.box {
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 100vh;
		padding: 20px;
		box-sizing: border-box;
	}
	.form {
		width: 1000px;
		margin: 20px 0;
	}
	.info {
		display: flex;
		flex-direction: column;
		width: 1000px;
	}
	p {
		display: flex;
		line-height: 40px;
		text-align: center;
		margin: 10px 0 0;
		align-items: flex-start;
		justify-content: center;
		background: #eafef5;
        padding: 10px 0;
        /deep/ .el-input{
            width: 300px;
            margin-right: 20px;
        }
	}
</style>



