import http from './http';

// 获取验证码
const getCode = (data) => http.post('login/verifycode', data);

// 登录 /api/login/sign

const loginUser = (data) => http.post('/login/sign',data).then(res =>{
    return res.data || []
}).catch(err=>{
    console.log(err);
    return err
})


const updateIteration = (data) => http.post('/iteration/update',data).then(res =>{
    console.log(res,'data')
    return res.data || []
}).catch(err =>{
    return Promise.reject(err);
})
const delIteration = (data) => http.post('/iteration/del',data).then(res =>{
    console.log(data,'data')
    return res.data || []
}).catch(err=>{
    return Promise.reject(err);
})

const queryIteration = () => http.post('/iteration/search').then(res =>{
    return res.data || []
}).catch(err=>{
    return Promise.reject(err);
})

/**
 *
*/

const updateCustomIteration = (data) => http.post('/iteration/custom/update',data).then(res =>{
    console.log(res,'data')
    return res.data || []
}).catch(err =>{
    return Promise.reject(err);
})
const delCustomIteration = (data) => http.post('/iteration/custom/del',data).then(res =>{
    console.log(data,'data')
    return res.data || []
}).catch(err=>{
    return Promise.reject(err);
})

const queryCustomIteration = () => http.post('/iteration/custom/search').then(res =>{
    return res.data || []
}).catch(err=>{
    return Promise.reject(err);
})




export const api = {
    getCode: getCode,
    loginUser:loginUser,
    updateCustomIteration:updateCustomIteration,
    delCustomIteration:delCustomIteration,
    queryCustomIteration:queryCustomIteration,
    updateIteration:updateIteration,
    delIteration:delIteration,
    queryIteration:queryIteration
}

