<template>
  <div class="content">
    <el-Button type="primary" class="go-back" @click="goBack()">返回</el-Button>
    <el-input type="number" :min="1" v-model="num" @change="change"></el-input>
    <el-form :inline="false"
             label-suffix=":"
             label-width="auto"
             size="small"
             class="form-demo"
             :model="form">
      <div v-for="(item, index) in form.data" :key="index">
        <el-form-item
          label="选项&答案"
          class="options"
          :prop="item.answer"
          required
        >
          <template>
            <el-radio-group v-model="item.answer">
              <el-radio
                v-for="(itm,i) in item.options"
                :label="itm.option"
                :key="i"
                @change="changeOptions"
              >
                <div class="answer">{{ itm.option }}</div>
                <textarea v-model="itm.content"></textarea>
              </el-radio>
            </el-radio-group>
            <el-button type="primary" @click="addOptions(item,index)">添加选项</el-button>
            <el-button type="primary" @click="delOptions(index)">删除选项</el-button>
          </template>
        </el-form-item>
      </div>
    </el-form>
  </div>
</template>

<script>
import { Select, Radio, RadioGroup, Form, FormItem } from 'element-ui'
export default {
  name: 'ForOptions',
  components: {
    Select,
    Radio,
    RadioGroup,
    Form,
    FormItem
  },
  data () {
    return {
      form: {
        data: []
      },
      num: 2
    }
  },
  created () {
    this.init()
  },

  methods: {
    init () {
      for (let i = 0; i < this.num; i++) {
        // initObj在这里定义，则不会出现数据都指向同一个内存的问题
        const initObj = {
          answer: '',
          options: [
            { option: 'A', content: '' },
            { option: 'B', content: '' },
            { option: 'C', content: '' },
            { option: 'D', content: '' }
          ]
        }
        this.form.data.push(initObj)
      }
    },
    change (val) {
      this.num = val
      this.form.data = []
      this.init()
    },
    changeOptions (val) {
      console.log(val, 'val')
    },
    // add-option&answer
    addOptions (item, index) {
      let options = this.form.data[index].options
      console.log(index, options, 'index')
      if (options.length > 25) {
        return this.$message('已到达上限值')
      }
      let len = options.length + 65
      let param = {
        option: '',
        content: ''
      }
      param.option = String.fromCharCode(len)
      options.push(param)
    },
    // del-option&answer
    delOptions (index) {
      let options = this.form.data[index].options
      if (options.length < 3) {
        return this.$message('已到达下限值')
      }
      options.pop({})
    },
    goBack () {
      history.back()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .form-demo{
    width: 400px;
  }
  .form-demo /deep/ .el-radio{
    display: inline-flex;
    margin-right: 0;
    margin-top: 10px;
  }
  .form-demo /deep/ .el-radio__label{
    display: inline-flex;
  }
</style>



