<template>
	<div>
		<h1>vue 案例集合</h1>
		<div class="menu">
			<div class="cell">
				<!-- 保利威 -->
				<div class="item">
					<el-button type="primary" @click="handleClick('baoliwei')">保利威视频</el-button>
				</div>
				<div class="item">
					<el-button type="primary" @click="handleClick('aliyun')">阿里云视频</el-button>
				</div>
				<div class="item">
					<el-button type="primary" @click="handleClick('face')">人脸识别</el-button>
				</div>
			</div>

			<div class="cell">
                <div class="item">
					<el-button type="primary" @click="handleClick('toast')">toast提示</el-button>
				</div>
				<div class="item">
					<el-button type="primary" @click="handleClick('tinymce')">tinymce</el-button>
				</div>
				<div class="item">
					<el-button type="primary" @click="handleClick('cropper')">裁剪图片</el-button>
				</div>
				<div class="item">
					<el-button type="primary" @click="handleClick('canvas')">canvas</el-button>
				</div>
				<div class="item">
					<el-button type="primary" @click="handleClick('animista')">css动画-animista</el-button>
				</div>
                <div class="item">
					<el-button type="primary" @click="handleClick('for-radio')">for循环中radio动态增删</el-button>
				</div>
			</div>
            <div class="cell">
				<div class="item">
					<el-button type="primary" @click="handleClick('mongodb')">数据库-简单-mongodb</el-button>
				</div>
                <div class="item">
					<el-button type="primary" @click="handleClick('mongodbMore')">数据库-复杂-mongodb</el-button>
				</div>
			</div>
             <div class="cell">
				<div class="item">
					<el-button type="primary" @click="handleClick('provide')">provide-inject</el-button>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		name: "index",
		data() {
			return {};
		},
		methods: {
			handleClick(type) {
				switch (type) {
					case "axios":
						this.$router.push({ path: "/Axios" });
						break;
					case "baoliwei":
						this.$router.push({ path: "/BaoLiWei" });
						break;
					case "aliyun":
						this.$router.push({ path: "/AliYun" });
						break;
					case "toast":
						this.$router.push({ path: "/Toast" });
						break;
					case "tinymce":
						this.$router.push({ path: "/Tinymce" });
						break;
					case "for-radio":
						this.$router.push({ path: "/ForRadio" });
						break;
					case "cropper":
						this.$router.push({ path: "/Cropper" });
						break;
					case "canvas":
						this.$router.push({ path: "/Canvas" });
						break;
					case "animista":
						this.$router.push({ path: "/Animista" });
						break;
					case "face":
						this.$router.push({ path: "/Face" });
						break;
                    case "mongodb":
                        this.$router.push({ path: "/MongoDBView" });
						break;
                    case "mongodbMore":
                        this.$router.push({ path: "/MongoDBMoreView" });
						break;
                    case "provide":
                        this.$router.push({ path: "/Parent" });
						break;
				}
			}
		}
	};
</script>
<style scoped lang="scss">
	.menu {
		width: 500px;
		// background: #a9b1f3;
		display: flex;
		flex-direction: row;
		// justify-content: center;
		// align-items: center;
		margin: 20px;
        .cell{
            margin: 20px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            background: #a9b1f3;
        }
		.item {
			width: calc(100% - 40px);
			padding: 20px;
			// border-bottom: 1px solid #ccc;
		}
	}
</style>



