<template>
	<div>
		<h1>层级一</h1>
		<h3>msg:{{msg.name}}</h3>
		<el-button type="primary" @click="change">change</el-button>
		<children />
	</div>
</template>
<script>
	import Children from "./children.vue";
	export default {
		name: "parent",
		components: {
			Children
		},
		provide() {
			return { msg: this.msg };
		},
		data() {
			return {
				msg: {
                    name:0
                }
			};
		},
		methods: {
			change() {
				this.msg.name +=1;
			}
		}
	};
</script>



