import Vue from "vue";
import messageComponent from "./index.vue";

// 使用Vue.extend(), 创建一个构造器 挂载到实例上
const messageConstructor = Vue.extend(messageComponent);

let instance;
let instances = [];
let seed = 1;
/**
 * @param {string} id 文本唯一id
 * @param {string} message 内容
 * @param {string} type 类型 info:默认提示，success:成功，error：失败，warning:警告
 * @param {string} iconClass 自定义图标样式，会覆盖type
 * @param {string} customClass 自定义类名
 * @param {boolean} center 是否居中
 * @param {boolean} showClose 是否显示关闭按钮
 * @param {boolean} dangerouslyUseHTMLString 是否是HTML片段
 * @param {number} offset toast 距离窗口顶部的偏移量
 */

const Toast = (options) => {
	options = options || {};
	if (typeof options === "string") {
		// 假如是一段字符串，拼接如下
		options = {
			message: options
		};
	}

	let id = "my_message_" + seed++; // 页面id

	let userOnClose = options.onClose;  //userOnClose用来存放用户设置关闭时的回调函数, 参数为被关闭的 message 实例

	options.onClose = function () {
		Toast.close(id, userOnClose);
	};

	// 创建message实例,此时数据还没有挂载呢，$el 属性目前不可见，无法访问到数据和真实的dom
	instance = new messageConstructor({
		data: options
	});

	instance.id = id; // 元素id

	instance.$mount();

	document.body.appendChild(instance.$el); // 插入toast提示信息元素

	let verticalOffset = options.offset || 20; // 偏移

	instances.forEach((item) => {
		verticalOffset += item.$el.offsetHeight + 16; // 多个消息提示的间隔 计算实际位置
	});

	instance.verticalOffset = verticalOffset;

	instance.visible = true; // 显示

	instance.$el.style.zIndex += seed; // z-index

	instances.push(instance);
	return instance;
};

// 给Message增加四个直接调用的方法
// 支持this.$message.success('xxx')方式调用，等同于this.$message({type: 'success',message: 'xxx'})
["success", "warning", "info", "error"].forEach((type) => {
	Toast[type] = (options) => {
		if (typeof options === "string") {
			// 假如是一段字符串，拼接如下
			options = {
				message: options
			};
		}
		options.type = type;
		return Toast(options);
	};
});

Toast.close = function (id, userOnClose) {
	let len = instances.length;
	let index = -1;
	let removedHeight;
	for (let i = 0; i < len; i++) {
		if (id === instances[i].id) {
			removedHeight = instances[i].$el.offsetHeight;
			index = i;
			if (typeof userOnClose === "function") {
				userOnClose(instances[i]);
			}
			instances.splice(i, 1);
			break;
		}
	}
	if (len <= 1 || index === -1 || index > instances.length - 1) return;
	for (let i = index; i < len - 1; i++) {
		let dom = instances[i].$el;
		dom.style["top"] = parseInt(dom.style["top"], 10) - removedHeight - 16 + "px";
	}
};

// 关闭所有消息提示
Toast.closeAll = function () {
	for (let i = instances.length - 1; i >= 0; i--) {
		instances[i].close();
	}
};

function toast() {
	window.toast = Vue.prototype.$toast = Toast; // 挂载全局
}
export default toast;



