import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/page/index'
import BaoLiWei from '@/components/bao-li-wei/index.vue'
import Toast from '@/components/toast/index.vue'
import Tinymce from '@/components/tinymce/index.vue'
import ForRadio from '@/components/for-radio/index.vue'
import AliYun from '@/components/a-li-yun-video/index.vue'
import Cropper from '@/components/cropper/index.vue'
import Axios from '@/components/axios/index.vue'
import Canvas from '@/components/canvas-demo/index.vue'
import LoginSuccess from '@/components/axios/success.vue'
import Animista from '@/components/css-animista/index.vue'
import Face from '@/components/face-recognition/index.vue'
import MongoDBView from '@/components/mongodb-view/index.vue'
import MongoDBMoreView from '@/components/mongodb-view/more.vue'
import Parent from '@/components/provides/parent.vue'

Vue.use(Router)

export default new Router({
  routes: [
       {
        path: '/Axios',
        name: 'Axios',
        component: Axios
    },
    {
        path: '/LoginSuccess',
        name: 'LoginSuccess',
        component: LoginSuccess
    },
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    // 保利威视频
    {
        path: '/BaoLiWei',
        name: 'BaoLiWei',
        component: BaoLiWei
    },
    // 阿里云视频
    {
        path: '/AliYun',
        name: 'AliYun',
        component: AliYun
    },
    // toast提示
    {
        path: '/Toast',
        name: 'Toast',
        component: Toast
      },
      // 富文本
    {
        path: '/Tinymce',
        name: 'Tinymce',
        component: Tinymce
      },
      // for循环中动态删减
    {
        path: '/ForRadio',
        name: 'ForRadio',
        component: ForRadio
    },
      // 裁剪图片
    {
        path: '/Cropper',
        name: 'Cropper',
        component: Cropper
    },
    // canvas
    {
        path: '/Canvas',
        name: 'Canvas',
        component: Canvas
    },
    // css 动画
    {
        path: '/Animista',
        name: 'Animista',
        component: Animista
    },
     // 人脸识别
     {
        path: '/Face',
        name: 'Face',
        component: Face
    },
     // 数据库简单操作
     {
        path: '/MongoDBView',
        name: 'MongoDBView',
        component: MongoDBView
    },
    // 数据库复杂操作
    {
        path: '/MongoDBMoreView',
        name: 'MongoDBMoreView',
        component: MongoDBMoreView
    },
    {
        path: '/Parent',
        name: 'Parent',
        component: Parent
    }
  ]
})



