<template>
	<div>
		<h1>层级三</h1>
		<h3>name:{{name}}}</h3>
		<h4>inject:{{getMsg.name}}</h4>
	</div>
</template>
<script>
	export default {
		name: "sub-children",
		inject: ["msg"],
		data() {
			return {
				name: "hello,subchildren",
                getMsg:this.msg
			};
		},
		watch: {
			'msg.name': {
				handler(val,old) {
                    this.getMsg.name = val
				},
				deep: true,
				immediate: true
			}
		}
	};
</script>



