<template>
	<div class="box">
		<!-- <div class="form" v-for="(item,index) in table" :key="index">
            <el-input type="text" v-model="item.name"></el-input>
            <el-input type="text" v-model="item.password"></el-input>
            <el-button type="text" @click="edit(item)">编辑</el-button>
            <el-button type="text" @click="del(item.uuid)">删除</el-button>
		</div>-->
		<div class="form" v-for="(item,index) in table" :key="index">
			<div>
				<span>id:{{item._id}}</span>
			</div>
			<div class="children" v-for="(children,i) in item.test" :key="i">
				<el-input type="text" v-model="children.name"></el-input>
				<el-input type="text" v-model="children.password"></el-input>
				<el-button type="text" @click="edit(item,children)">编辑</el-button>
				<el-button type="text" @click="del(item._id,children.uuid)">删除</el-button>
			</div>
			<el-button type="primary" @click="addChildren(index)">新增</el-button>
		</div>
		<el-button type="primary" @click="add">新增</el-button>
	</div>
</template>
<script>
	import { api } from "../../api/api.js";
	// import axios from 'axios';
	export default {
		name: "mongodb-view",
		data() {
			return {
				form: {
					name: "",
					password: ""
				},
				list: [],
				id: "",
				table: []
			};
		},
		created() {
			this.table = [
				{
					_id: "",
					test: [
						{
							name: "",
							password: ""
						}
					]
				}
			];
			this.query();
		},
		methods: {
			add() {
				this.table.push({
					_id: "",
					test: [
						{
							name: "",
							password: ""
						}
					]
				});
			},
			addChildren(index) {
				this.table[index].test.push({
					name: "",
					password: ""
				});
			},
			edit(item, children) {
				let params = {
					id: item._id,
					...children
				};
				api.updateIteration(params).then(res => {
					if (res) {
						this.$toast.success("更新成功");
						this.query();
					}
				});
			},
			del(id, uuid) {
				api.delIteration({ id: id, uuid: uuid }).then(res => {
					console.log(res, "res-del");
					this.$toast.success("删除成功");
					this.query();
				});
			},
			query() {
				api.queryIteration().then(res => {
					// this.id =res._id;
					this.table = res;
					console.log(res, "res-query");
				});
			}
		}
	};
</script>
<style scoped lang="scss">
	.box {
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 100vh;
		padding: 20px;
		box-sizing: border-box;
	}
	.form {
		width: 1000px;
		margin: 20px 0;
		display: flex;
		flex-direction: column;
        align-items: flex-start;
	}
    .children{
        display: flex;
        flex-direction: row;
    }
	// .info {
	// 	display: flex;
	// 	flex-direction: column;
	// 	width: 1000px;
	// }
	// p {
	// 	display: flex;
	// 	line-height: 40px;
	// 	text-align: center;
	// 	margin: 10px 0 0;
	// 	align-items: flex-start;
	// 	justify-content: center;
	// 	background: #eafef5;
	//     padding: 10px 0;
	//     /deep/ .el-input{
	//         width: 300px;
	//         margin-right: 20px;
	//     }
	// }
</style>



