<template>
    <div class="main" v-loading="loading">
        <div class="content-main">
            <div>
                验证码：
                <img :src="codes.code" @click="getCode()" alt />
            </div>
            <div class="user">
                <el-input type="text" v-model="user.userName" placeholder="请输入用户名"></el-input>
            </div>
            <div class="psd">
                <el-input type="password" v-model="user.password" placeholder="请输入密码"></el-input>
            </div>
            <div class="psd">
                <el-input type="text" v-model="user.code" placeholder="请输入验证码"></el-input>
            </div>
            <div>
                <el-button class="login-name" v-loading="loginLoading" @click="deLogin">登录</el-button>
            </div>
        </div>
    </div>
</template>
<script>
import { api } from "../../api/api.js";
export default {
    data(){
        return{
            loading:false,
            loginLoading:false,
            user:{
                userName: "gejj",
                password: "Gejj123456",
                code: ""
            },
            codes:{
                code: "",
                guid: ""
            }
        }
    },
    mounted(){
        this.getCode();
    },
    methods:{
        getCode(){
           this.loading = true;

            api.getCode(this.user)
                .then(res => {
                    this.codes = res.data.data;
                    this.loading = false;
                })
                .catch(err => {
                    console.log(err, "err");
                    this.loading = false;
                });
        },
        deLogin(){
             let loginInfo = {
                userName: this.user.userName,
                password: this.user.password,
                code: this.user.code,
                guid: this.codes.guid
            };
            console.log(loginInfo, "log");

            this.loginLoading = true;

            api.loginUser(loginInfo)
                .then(res => {
                    console.log(res, "res");
                    localStorage.setItem("token", res.data.token);
                    localStorage.setItem("user", JSON.stringify(res.data));
                    this.loginLoading = false;

                    this.$router.push({path: "/LoginSuccess",});
                })
                .catch(err => {
                    console.log("登录失败", err);
                    this.loginLoading = false;
                });
        }
    },
};
</script>
<style scoped lang="scss">
.main {
    width: 500px;
    margin: 20% auto 0;
    background: #fff;
    .login-name {
        background-color: #998aef;
        height: 50px;
        text-align: center;
        color: #fff;
        width: 100%;
        font-size: 18px;
    }
    .content-main {
        padding: 20px 20px 20px;
        border: 1px solid#eee;
    }
    .user,
    .psd,
    .login-name {
        margin-top: 20px;
    }
}
</style>



