<template>
    <div>
        <h1>层级二</h1>
        <h3>name:{{name}}</h3>
        <sub-children/>
    </div>
</template>
<script>
import SubChildren from "./sub-children.vue"
export default {
    name:"children",
     components:{
        SubChildren
    },
    data(){
        return {
            name:'hello'
        }
    },
}
</script>



