<template>
	<div>
		<h1 class="title">阿里云视频</h1>
		<div>
			<el-button class="go-back" @click="goBack">返回</el-button>
		</div>
		<div>
			<div class="prism-player" id="J_prismPlayer"></div>
		</div>
	</div>
</template>
<script>
	export default {
		name: "a-li-yun",
		data() {
			return {
				player: null
			};
		},
		mounted() {
			this.init();
		},
		methods: {
			goBack() {
				history.back();
			},
			// 集成
			init() {
				this.player = new Aliplayer(
					{
						id: "J_prismPlayer", // 播放器容器
						width: "96%", // 播放器宽
						height: "700px", // 播放器高
						videoWidth: "100%", // 视频宽
						videoHeight: "85%", // 视频高
						language: "zh-cn",
						autoplay: false, // 自动播放
						isLive: false, // 是否为直播
						rePlay: true, // 播放器自动循环播放。
						useH5Prism: true, // 指定使用H5播放器。
						// useFlashPrism:false, // 定使用Flash播放器。
						playsinline: true, // H5是否内置播放，有的Android浏览器不起作用。
						showBuffer: true, // 显示播放时缓冲图标，默认值为true
						controlBarVisibility: "hover", // 控制面板的实现，默认值为：hover。取值：click：点击。hover：停留。always：一直。never：隐藏整个控制面板。
						showBarTime: "", // 控制栏自动隐藏时间，单位毫秒。
						mediaType: "video", //指定返回音频还是视频，只有使用vid的播放方式时支持，默认值为video。取值：video：视频。audio：针对只包含音频的视频格式，比如音频的MP4。仅H5支持。
						/**
						 * definition显示视频清晰度，多个使用英文逗号（,）分隔，比如：’FD,LD’，此值是vid对应流清晰度的一个子集，取值：
						 * FD（流畅）
						 * LD（标清）
						 * SD（高清）
						 * HD（超清）
						 * OD（原画）
						 * 2K（2K）
						 * 4K（4K）
						 */
						definition: "FD,LD,SD,HD",
						defaultDefinition: "FD",
						x5_video_position: "center",
						// 支持播放地址播放，此方式播放优先级最高
						// source : 'http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4',
						source: JSON.stringify({
							HD: "//player.alicdn.com/resource/player/qupai.mp4",
							SD: "//player.alicdn.com/video/editor.mp4"
						}),
						// 播放方式二：推荐点播用户使用
						vid: "1e067a2831b641db90d570b6480fbc40",
						playauth: "ddd", //播放权证，获取播放权重请参见获取视频播放凭证。
						cover:
							"http://liveroom-img.oss-cn-qingdao.aliyuncs.com/logo.png",
						// encryptType: 1, //当播放私有加密流时需要设置。
						// 播放方式三：仅MPS用户使用
						// vid : '1e067a2831b641db90d570b6480fbc40', // 媒体转码服务的媒体ID。
						// accId: 'dd',
						// accSecret: 'dd',
						// stsToken: 'dd',
						// domainRegion: 'dd',
						// authInfo: 'dd',
						// 播放方式四：使用STS方式播放
						// vid : '1e067a2831b641db90d570b6480fbc40',
						// accessKeyId: 'dd',
						// securityToken: 'dd',
						// accessKeySecret: 'dd',
						// region: 'cn-shanghai', // eu-central-1, ap-southeast-1,
						// 播放方式五：使用DRM方式播放
						// isDrm: true,
						// vid: '34ecdedd081c426383cd66782de6656d',
						// accessKeyId: 'dd',
						// securityToken: 'dd',
						// accessKeySecret: 'dd',
						// region: 'cn-shanghai', // eu-central-1, ap-southeast-1,
						// certId: 'dd', // 用于请求苹果证书
						// components: [
						// 	{
						// 		name: "QualityComponent",
						// 		type: AliPlayerComponent.QualityComponent
						// 	}
						// ]
					},
					player => {
						console.log("The player is created.");
                        player.on('play',()=>{
                            console.log(111111111111)
                        })
					}
				);
			}
		}
	};
</script>
<style scoped lang="scss">
	.prism-player {
		margin: 0 auto;
		// height: 500px;
		border: 1px solid #ccc;
		background: #000;
		position: relative;
		color: #fff;
	}
</style>



